import {
	getBarnoInfo,
	getMC02D1ByBarcode,
	getPB08HByMtarea,
	getMC02_231
} from '@/api/api.js'
export default {
	props: {
		title: {
			type: String,
			default: "车载MES"
		}
	},
	data() {
		return {
			heightH: uni.getSystemInfoSync().windowHeight,
			result: "",
			result1: "",
			scanData: new Object(),
			scanData1: new Object(),
			ckwScanFlag:true,//从库位扫码
			psNum:0
		}
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: this.title
		})
	},
	methods: {
		scan: function() {
			uni.scanCode({
				success: (res) => {
					this.view = true;
					this.url = "http://39.108.215.49:8009/html/2.html?res=" + res.result;
					this.result = res.result
					getBarnoInfo({
						el_barno: res.result
					}).then(res => {
						this.ckwScanFlag=true;
						console.log(res);
						this.scanData = res.DicResult;
						console.log(this.scanData)
					})
				}
			});
		},
		/* 库位扫码 */
		scan1: function() {
			if(!this.ckwScanFlag){
				uni.showToast({
					title:'请扫码料车条码',
					icon:'none'
				})
				return;
			}
			uni.scanCode({
				success: (res) => {
					this.view = true;
					this.url = "http://39.108.215.49:8009/html/2.html?res=" + res.result;
					this.result1 = res.result
					getPB08HByMtarea({
						mt_area: 'TMP_AREA2'
					}).then(res1 => {
						console.log(res1)
						getMC02D1ByBarcode({
							el_barno: this.result,
							mt_area: 'TMP_AREA2'
						}).then(res2=>{
							this.scanData1 = res2.DicResult;
							this.psNum=res2.DicResult.mc_qty;
							console.log(res2);
						})
						console.log(this.scanData1)
					})
				}
			});
		},
		commit(){
			// if(this.scanData1.mc_qty<this.psNum){
			// 	uni.showToast({
			// 		title:'备料数量过多',
			// 		icon:'none'
			// 	})
			// 	return;
			// }else if(this.psNum<=0){
			// 	uni.showToast({
			// 		title:'备料数量要大于0',
			// 		icon:'none'
			// 	})
			// 	return;
			// }
			getMC02_231({
				"mc_list": this.scanData1.mc_list,
				"el_no": this.scanData.el_no,
				"mt_qty": this.scanData1.mt_qty,
				"mt_area": this.scanData1.mt_area,
				"mt_rarea": this.result1,
				"lot_no": this.scanData.lot_no,
				"emplid": "063871",
				"ruid": this.scanData1.ruid
			}).then(res=>{
				console.log(res);
			});
		}
	}
}
